<template>
    <div class="header">
        <!-- 折叠按钮 -->
        <div class="sys-logo" @click="backToIndex">
            <i class="el-icon-menu"></i>
        </div>
        <div class="sys-name">联通公众号管理系统</div>
        <div class="header-right">
            <div class="header-user-con">
                <!-- 全屏显示 -->
                <div class="btn-fullScreen" @click="handleFullScreen">
                    <el-tooltip effect="dark" :content="fullScreen?`取消全屏`:`全屏`" placement="bottom">
                        <i class="el-icon-rank"></i>
                    </el-tooltip>
                </div>
                <!-- 消息中心 -->
                <div class="btn-bell">
                    <el-tooltip effect="dark" :content="message?`有${message}条未读消息`:`消息中心`" placement="bottom">
                        <router-link to="/tabs">
                            <i class="el-icon-bell"></i>
                        </router-link>
                    </el-tooltip>
                    <span class="btn-bell-badge" v-if="message"></span>
                </div>
                <!-- 用户头像 -->
                <div class="user-avator" @click="toMineInfo"><head-image class="avator" :url="userInfo.headImg" :styles="{borderRadius: '100%',width: '40px',height: '40px'}" /></div>
                <!-- 用户名下拉菜单 -->
                <el-dropdown class="user-name" trigger="click" @command="handleCommand">
                    <span class="el-dropdown-link">
                      {{userInfo.username}} <i class="el-icon-caret-bottom" ></i>
                    </span>

                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item divided  command="logout">退出登录</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
            </div>
        </div>
    </div>
</template>
<script>
import {mapState} from 'vuex'
import HeadImage from '@/components/HeadImage'
export default {
  components: {HeadImage},
  data () {
    return {
      fullScreen: false,
      message: 2
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.manager.manager
    })
  },
  methods: {
    // 用户名下拉菜单选择事件
    handleCommand (command) {
      if (command === 'logout') {
        this.$bus.$emit('show-loading-dialog')
        this.$store.dispatch('manager/logout').then(() => {
          location.pathname = this.$const.loginPath
        }).catch(reason => {
          this.$message.error(reason)
        }).finally(() => {
          this.$bus.$emit('hide-loading-dialog')
        })
      }
    },
    // 侧边栏折叠
    backToIndex () {
      this.$router.replace({name: 'index'})
    },
    // 全屏事件
    handleFullScreen () {
      let element = document.documentElement
      if (this.fullScreen) {
        if (document.exitFullscreen) {
          document.exitFullscreen()
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen()
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen()
        } else if (document.msExitFullscreen) {
          document.msExitFullscreen()
        }
      } else {
        if (element.requestFullscreen) {
          element.requestFullscreen()
        } else if (element.webkitRequestFullScreen) {
          element.webkitRequestFullScreen()
        } else if (element.mozRequestFullScreen) {
          element.mozRequestFullScreen()
        } else if (element.msRequestFullscreen) {
          // IE11
          element.msRequestFullscreen()
        }
      }
      this.fullScreen = !this.fullScreen
    },
    /**
     * 跳转到我的消息页面
     */
    toMineInfo () {
      this.$router.push({name: 'mine'})
    }
  }
}
</script>
<style scoped lang="less">
  @import url(../assets/stylesheet/common);
    .header {
        position: relative;
        box-sizing: border-box;
        width: 100%;
        height: 70px;
        font-size: 22px;
        background: @brand;
        color: #fff;
    }
    .sys-logo{
        float: left;
        padding: 0 21px;
        cursor: pointer;
        line-height: 70px;
    }
    .header .sys-name{
        float: left;
        width:250px;
        line-height: 70px;
    }
    .header-right{
        float: right;
        padding-right: 50px;
    }
    .header-user-con{
        display: flex;
        height: 70px;
        align-items: center;
    }
    .btn-fullScreen{
        transform: rotate(45deg);
        margin-right: 5px;
        font-size: 24px;
    }
    .btn-download {
      font-size: 24px;
      margin: 5px;
    }
    .btn-download,.btn-bell,.btn-fullScreen{
        position: relative;
        width: 30px;
        height: 30px;
        text-align: center;
        border-radius: 15px;
        cursor: pointer;
    }
    .btn-bell-badge{
        position: absolute;
        right: 0;
        top: -2px;
        width: 8px;
        height: 8px;
        border-radius: 4px;
        background: #f56c6c;
        color: #fff;
    }
    .btn-bell .el-icon-bell{
        color: #fff;
    }
    .user-name{
        margin-left: 10px;
    }
    .user-avator{
      cursor: pointer;
      margin-left: 20px;
      .avator{
        width: 48px;
        height: 48px;
      }
    }
    .user-avator img{
        display: block;
        width:40px;
        height:40px;
        border-radius: 50%;
    }
    .el-dropdown-link{
        color: #fff;
        cursor: pointer;
    }
    .el-dropdown-menu__item{
        text-align: center;
    }
</style>



